import { Component } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { loginRequest, loginResponse } from "app/auth/models/login";
import { AssetsManager } from "app/shared/resources/assets-manager";
import { InputAppearance } from "app/shared/enums/mat-theme.enum";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { Storage } from "app/shared/resources/local-storage";
import { IResponse } from "app/shared/interfaces/responces";

@Component({
  selector: "app-login-page",
  templateUrl: "./login-page.component.html",
  styleUrls: ["./login-page.component.scss"],
})
export class LoginPageComponent extends AppHelpers {
  loginFormSubmitted = false;
  isLoginFailed = false;
  appAsset: AssetsManager = new AssetsManager();
  form: FormGroup;
  appearance = InputAppearance;
  returnUrl: string;

  constructor(private route: ActivatedRoute) {
    super();
    this.returnUrl = this.route.snapshot.queryParams["returnUrl"];
  }

  ngOnInit() {
    this.form = this.fb.group({
      username: ["", [Validators.required]],
      password: ["", [Validators.required]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onSubmit() {
    this.loginFormSubmitted = true;
    if (this.form.valid) {
      this.showLoading();
      let bodyRequest = {} as loginRequest;
      bodyRequest.name = this.formCtrls.username.value;
      bodyRequest.password = this.formCtrls.password.value;

      this.authService.login(bodyRequest).subscribe((res: loginResponse | IResponse | any) => {
          if (res.message.messageType == MessageTypes.Success) {
            Storage.setToken(res.jwtToken);
            this.router.navigateByUrl(this.returnUrl ?? "/home");
            this.authService.isUserLogin$.next(true);
            this.hideLoading();
          }
          else this.errorList(res?.message?.messages);
        },
        (error) => {
          this.errorList(error?.message?.messages);
        }
      )
      ;
    }
  }
}
